.about-page {
	padding: 30px 0;
}

.about-page__title {
	position: relative;
	max-width: 740px;
	padding-left: 15px;
	color: #000;
	font-size: 18px;
	line-height: 20px;
	font-weight: 700;
	text-transform: uppercase;
	&::before {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		height: 100%;
		width: 5px;
		border-radius: 67px;
		background: #be0026;
	}
}

.about-page__title-descr {
	padding: 15px 0 25px;
	max-width: 740px;
	color: #000;
	font-size: 14px;
	font-weight: 300;
}

.about-page__item {
	display: flex;
	flex-direction: column;
	row-gap: 20px;
	&:not(:last-child) {
		margin-bottom: 25px;
	}
}

.about-page__text-block {
	color: #000;
	text-align: justify;
	font-size: 14px;
	font-weight: 400;
	line-height: 21px;
	& p:not(:last-child) {
		margin-bottom: 15px;
	}
	& p span {
		color: #f00;
	}
}

.about-page__img {
	position: relative;
	width: 100%;
	overflow: hidden;
	flex: 0 0 300px;
	& img {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.about-page__list {
	font-weight: 500;
	& li {
		padding-left: 45px;
		font-weight: 400;
	}
	& li:first-child{
		margin-top: 10px;
	}
	& li:not(:last-child) {
		margin-bottom: 10px;
	}
}

@media (min-width: 961px) {
	.about-page__item {
		flex-direction: row;
		column-gap: 20px;
	}
	.about-page__img {
		flex: 0 0 50%;
	}
}
