.news-item {
	padding: 20px 0;
	& h1 {
		margin: 10px 0;
		font-size: 25px;
		font-weight: 500;
	}
	p {
		margin: 10px 0;
		text-align: justify;
		font-size: 16px;
		font-weight: 300;
		line-height: 24px;
	}
	& figure {
		margin: 10px 0;
	}
	& img {
		width: 100%;
		height: auto;
	}
	& figcaption {
		margin-top: 7px;
		text-align: center;
		color: #484848;
		font-size: 14px;
		
	}
}
