.cooperation-page {
	padding: 30px 0;
}

.cooperation-page__title {
	max-width: 890px;
}

.cooperation-page__list {
	display: flex;
	flex-direction: column;
	row-gap: 30px;
	padding: 30px 0;
}

.cooperation-page__item {
	display: flex;
	flex-direction: column-reverse;
	row-gap: 20px;
}

.cooperation-page__text-block {
	& h2 {
		margin-bottom: 25px;
		color: #000;
		font-size: 18px;
		font-weight: 500;

		-webkit-line-clamp: 3; /* Число отображаемых строк */
		display: -webkit-box; /* Включаем флексбоксы */
		-webkit-box-orient: vertical; /* Вертикальная ориентация */
		overflow: hidden; /* Обрезаем всё за пределами блока */
	}
	& p {
		color: #000;
		text-align: justify;
		font-size: 16px;
		font-weight: 300;
		line-height: 24px;

		-webkit-line-clamp: 10; /* Число отображаемых строк */
		display: -webkit-box; /* Включаем флексбоксы */
		-webkit-box-orient: vertical; /* Вертикальная ориентация */
		overflow: hidden; /* Обрезаем всё за пределами блока */
	}
}

.cooperation-page__link {
	display: inline-block;
	margin-top: 50px;
	padding: 10px 20px;
	border-radius: 5px;
	background-color: $accent;
	color: $accent-red;
	font-weight: 600;
	text-transform: uppercase;
	transition: all 0.3s ease-in-out;
	&:hover {
		background-color: #29b856;
		color: #790018;
	}
}

.cooperation-page__img {
	position: relative;
	flex: 0 0 350px;
	width: 100%;
	overflow: hidden;
	& img {
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0;
		right: 0;
		object-fit: cover;
	}
}

.cooperation-page__img-data {
	position: absolute;
	z-index: 2;
	bottom: 0;
	left: 0;
	width: 100%;
	padding: 8px 0 8px 20px;
	background: #229546;
	color: #fff;
	font-size: 20px;
	font-style: normal;
	font-weight: 500;
	line-height: normal;
}

.cooperation-page__appeal {
	position: relative;
	margin-top: 30px;
	background-color: $accent;
	padding: 60px 60px 60px 100px;
	max-width: 750px;

	color: #fff;
	text-align: justify;

	font-size: 20px;
	font-weight: 400;
	line-height: 30px;
	&::before {
		content: "";
		position: absolute;
		z-index: 2;
		height: 100%;
		width: 60px;
		left: 20px;
		top: 0;
		background-image: url(../img/cooperation/appeal-element.svg);
		background-repeat: repeat-y;
	}
	& span{
		font-weight: 600;
		font-size: 22px;
	}
}

@media (min-width: 961px) {
	.cooperation-page__item {
		flex-direction: row;
		column-gap: 50px;
	}

	.cooperation-page__text-block {
		position: relative;
		&::after {
			content: "";
			position: absolute;
			right: calc(-50px / 2 - 3px / 2);
			top: 0;
			height: 100%;
			width: 3px;
			border-radius: 3px;
			background-color: $accent;
		}
	}

	.cooperation-page__img {
		flex: 0 0 50%;
	}
}
