.leaders {
	background: #d9d9d9;
	padding-top: 50px;
	padding-bottom: 100px;
}

.leaders__title {
	padding-bottom: 50px;
	font-size: clamp(23px, 5vw, 25px);
	color: #000;
	text-align: center;
	font-weight: 600;
	text-transform: uppercase;
}

.leaders__list {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	text-align: center;
	gap: 70px;
}

.leader {
	flex: 0 1 500px;
}

.leader__img {
	position: relative;
	width: 228px;
	height: 228px;
	border-radius: 50%;
	overflow: hidden;
	margin: 0 auto;
	margin-bottom: 12px;
	& img {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
	}
}

.leader__name {
	position: relative;
	border-bottom: solid 5px $accent;
	min-height: 50px;
	padding-bottom: 4px;
	color: #000;
	text-align: center;
	font-size: 20px;
	font-weight: 600;
	text-transform: uppercase;
	& span{
		position: absolute;
		width: 100%;
		left: 0;
		top: 50%;
		transform: translateY(-50%);
	}
}
.leader__post {
	min-height: 40px;
	position: relative;
	padding-top: 12px;
	color: #000;
	text-align: center;
	font-size: 20px;
	font-weight: 400;
	& span{
		padding-top: 15px;
		position: absolute;
		width: 100%;
		left: 0;
		top: 50%;
		transform: translateY(-50%);
	}
}

@media (min-width: 641px) {
	.leader {
		flex: 0 1 264px;
	}

	.leader__name{
	
	}
	.leaders__list{
		column-gap: 20px;
	}
}
