
.header__head {
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-top: solid 4px $accent;
	border-bottom: solid 4px $accent;
}

.header__logo {
	& a {
		position: relative;
		display: block;
		width: 160px;
		height: 59px;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
}

.header__decription {
	display: none;
	padding-right: 10px;
	color: $accent;
	text-align: right;
	text-shadow: 1px 1px 1px rgba(255, 255, 255, 0.78);
	font-size: 15px;
	line-height: 1.1;
	font-style: normal;
	font-weight: 500;
	text-transform: uppercase;
	border-right: 13px solid #be0026;
}

@media (min-width: 1025px) {
	.header__decription {
		display: block;
		padding-right: 10px;
		color: $accent;
		text-align: right;
		text-shadow: 1px 1px 1px rgba(255, 255, 255, 0.78);
		font-size: 15px;
		line-height: 1.1;
		font-style: normal;
		font-weight: 500;
		text-transform: uppercase;
		border-right: 13px solid #be0026;
	}
}
