.list-news {
	padding: 30px 0;
	position: relative;
}

.list-news_title {
	margin-bottom: 20px;
	font-size: 24px;
	font-weight: 700;
}

.news-list {
	width: 100%;
	// border: solid green 4px;
}
.news-list__item {
	width: 100%;
	padding: 10px 0;
	// border: solid red 1px;
}

.news-list__img {
	position: relative;
	width: 100%;
	padding-bottom: 100%;
	object-fit: cover;
	& img {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.news-list__date {
	position: relative;
	margin: 5px 0 10px 0;
	color: #9b9b9b;
	font-size: 13px;
	font-weight: 500;
	&::after {
		content: "";
		margin-left: 65px;
		position: absolute;
		width: calc(100% - 65px);
		height: 3px;
		background-color: $accent;
		left: 0;
		top: 5px;
	}
}

.news-list__title {
	color: #000;
	font-size: 16px;
	font-weight: 500;

	-webkit-line-clamp: 3; /* Число отображаемых строк */
	display: -webkit-box; /* Включаем флексбоксы */
	-webkit-box-orient: vertical; /* Вертикальная ориентация */
	overflow: hidden; /* Обрезаем всё за пределами блока */
}
.news-list__descr {
	margin-top: 10px;
	color: #000;
	font-size: 16px;
	font-weight: 300;

	-webkit-line-clamp: 10; /* Число отображаемых строк */
	display: -webkit-box; /* Включаем флексбоксы */
	-webkit-box-orient: vertical; /* Вертикальная ориентация */
	overflow: hidden; /* Обрезаем всё за пределами блока */
}

@media (min-width: 741px) {
	.news-list {
		margin: 0 -10px;
	}

	.news-list__item {
		width: 50%;
		padding: 10px 10px;
	}
	.news-list__img {
		padding-bottom: 120%;
	}
}

@media (min-width: 1160px) {
	.news-list__item {
		width: 33.3%;
	}
}
