.posting {
	padding: 40px 0;
}
.post__title {
	padding-left: 15px;
	border-left: 5px solid $accent-red;
	color: #000;
	font-size: 20px;
	font-weight: 600;
}

.post__text {
	max-width: 730px;
	margin: 15px 0;
	color: #000;
	font-size: 18px;
	font-weight: 300;
}

.post__img {
	position: relative;
	width: 100%;
	padding-bottom: 62%;
	& img {
		position: absolute;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

@media (min-width: 1281px) {
	.post{
		display: flex;
		column-gap: 20px;
	}

	.post__img {
		flex: 0 0 760px;
		height: 440px;
		padding-bottom: 0;
	}
}
