//<ПЕРЕМЕННЫЕ>=============================================================
$minWidth: 320px;
$maxWidth: 1920;
$maxWidthContainer: 1150;
$md1: $maxWidthContainer + 12;
$md2: 991.98;
$md3: 767.98;
$md4: 479.98;

$main-text: #000;
$secondary-text: #606774;
$accent: #229546;
$accent-red: #BE0026;
// $black: #202328;
//</ПЕРЕМЕННЫЕ>============================================================

//<ОБНУЛЕНИЕ, ОБЩИЕ ПАРАМЕТРЫ>=============================================
@import "help/null";

body {
	color: $main-text;
	font-size: 16px;
	font-style: normal;
	font-weight: 400;
	&._lock {
		overflow: hidden;
	}
}
//</ОБНУЛЕНИЕ, ОБЩИЕ ПАРАМЕТРЫ>============================================

//<ОБОЛОЧКА>================================================================
.wrapper {
	position: relative;
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	// justify-content: space-between;
}
//</ОБОЛОЧКА>===============================================================

//</ОСНОВНАЯ СЕТКА>=========================================================
._container {
	max-width: $maxWidthContainer + px;
	padding: 0 15px;
	margin: 0 auto;
}
._container_item{
	max-width: 740px;
	padding: 0 15px;
	margin: 0 auto;
}
//</ОСНОВНАЯ СЕТКА>=========================================================

//<ЭЛЕМЕНТЫ>================================================================
.page__title {
	position: relative;
	max-width: 740px;
	padding-left: 15px;
	color: #000;
	font-size: 18px;
	line-height: 20px;
	font-weight: 700;
	text-transform: uppercase;
	&::before {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		height: 100%;
		width: 5px;
		border-radius: 67px;
		background: #be0026;
	}
}
//<ЭЛЕМЕНТЫ>================================================================

