.main {
	background-color: #fff;
}

// ===================.slider-news==================

.slider-news {
	position: relative;
	&__link {
	}
	&__img {
		position: relative;
		width: 100%;
		height: 500px;
		& img {
			width: 100%;
			height: 100%;
			position: absolute;
			object-fit: cover;
			object-position: center center;
		}
		&:before {
			content: "";
			width: 70%;
			height: 100%;
			top: 0;
			left: 0;
			position: absolute;
			z-index: 3;
			background: linear-gradient(89.56deg, #000000 32.89%, rgba(0, 0, 0, 0) 97.83%);
		}
	}
	&__link {
		position: absolute;
		padding: 0 20px 0 75px;
		z-index: 5;
		font-weight: 450px;
		left: 0;
		top: 50%;
		transform: translate(0%, -50%);
	}
	&__title {
		font-size: 28px;
		text-transform: uppercase;
		letter-spacing: 2px;
		line-height: 30px;
		color: #fff;
		padding-left: 10px;
		max-width: 500px;
		border-left: 10px solid transparent;
		&:hover {
			border-left: 10px solid red;
		}
	}
}

.slider-news .slick-dots {
	position: absolute;
	display: flex;
	bottom: 5px;
	left: 50%;
	transform: translateX(-50%);
	z-index: 3;
}

.slider-news .slick-dots button {
	background: #2e3a59;
	border-radius: 50%;
	font-size: 0;
	height: 15px;
	width: 15px;
	margin: 10px;
	outline: none;
}

.slider-news .slick-dots .slick-active button {
	background: #d3d3d3;
}

.slider-news .slick-arrow {
	position: absolute;
	top: 0;
	z-index: 10;
	font-size: 0;
	width: 80px;
	height: 100%;
}
.slick-prev {
}

.slider-news .slick-arrow.slick-prev {
	left: 0;
	background: url("../img/icons/arrow-slider-prev.svg");
	background-repeat: no-repeat;
	background-position: 60px 475px;
	background-color: transparent;
}

.slider-news .slick-arrow.slick-next {
	right: 0;
	background: url("../img/icons/arrow-slider-next.svg");
	background-repeat: no-repeat;
	background-position: 10px 475px;
	background-color: transparent;
}
.slider-news .slick-arrow.slick-prev:hover,
.slider-news .slick-arrow.slick-next:hover {
	background-color: rgba(255, 255, 255, 0.253);
}

._touch .slider-news .slick-arrow.slick-prev:hover,
._touch .slider-news .slick-arrow.slick-next:hover {
	background-color: transparent;
}

@media (max-width: 900px) {
	.slider-news {
		&__img {
			height: 400px;
			&:before {
				width: 90%;
			}
		}
		&__link {
			padding-left: 30px;
		}
		&__title {
			max-width: 400px;
			font-size: 20px;
			letter-spacing: 0.5px;
			line-height: 23px;
		}
	}
	.slider-news .slick-arrow.slick-prev {
		background-position: 60px 375px;
	}
	.slider-news .slick-arrow.slick-next {
		background-position: 10px 375px;
	}
}

@media (max-width: 450px) {
	.slider-news {
		&__img {
			height: 300px;
			position: relative;
			&:before {
				width: 120%;
			}
		}
		&__title {
			font-size: 20px;
			letter-spacing: 0.5px;
			line-height: 23px;
		}
	}
	.slider-news .slick-arrow.slick-prev {
		background-position: 20px 275px;
	}
	.slider-news .slick-arrow.slick-next {
		background-position: 50px 275px;
	}
}
// ===================================================================
// ===================study===========================================
// ===================================================================

.study {
	&__container {
	}
	&__main-title {
		padding: 0 0 15px 15px;
		font-weight: bold;
		font-size: 35px;
		text-transform: uppercase;
		color: #797979;
	}
	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}
	&__item {
		position: relative;
		flex: 1 0 30%;
		max-width: 700px;
		padding: 5px;
		margin: 10px;
		border: 2px solid transparent;
		border-radius: 5px;
		box-shadow: 1px 2px 7px -1px rgb(0 0 0 / 50%);
		transition: 0.3s;
		&:hover {
			border: 2px solid rgba(0, 0, 0, 0.2);
			box-shadow: none;
		}
		&:hover .study__img img {
			width: 105%;
			height: 105%;
		}
	}
	&__link {
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
	}
	&__img {
		overflow: hidden;
		position: relative;
		min-width: 350px;
		min-height: 225px;
		border-radius: 5px;
		& img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center center;
			position: absolute;
			top: 0;
			left: 0;
			transition: 1s;
		}
	}
	&__title {
		font-size: 18px;
		line-height: 21px;
		font-weight: 500;
		margin: 10px 0 40px 0;
		padding: 0 10px;
		color: #000000;
	}
}
.study .linktopage {
	padding-bottom: 10px;
}

@media (max-width: 899px) {
	.study {
		&__main-title {
			font-size: 25px;
			padding-left: 0px;
			text-align: center;
		}
		&__item {
			flex: 0 1 80%;
		}
		&__img {
			padding-bottom: 52%;
		}
	}
}

@media (max-width: 480px) {
	.study {
		&__img {
			min-height: 140px;
			min-width: 280px;
		}
	}
}

// ===================================================================
// ===================video-main======================================
// ===================================================================

.video-main .linktopage {
	padding-bottom: 10px;
}

.video-main {
	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}
	&__item {
		flex: 1 1 30%;
		max-width: 466px;
		margin: 5px;
		background: #939393;
	}
	&__description {
		font-size: 18px;
		line-height: 21px;
		font-weight: 300;
		letter-spacing: 0.3px;
		padding: 10px 10px;
		color: #fff;
		background: #939393;
		border-top: 10px solid #fff;
	}
}

@media (max-width: 899px) {
	.video-main {
		&__linktovideos {
			font-size: 25px;
			padding-left: 0px;
			text-align: center;
		}
	}
}

.video {
	min-width: 280px;
	position: relative;
	aspect-ratio: 16 / 9;
	&__link {
	}
	&__media {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
	&__button {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 68px;
		height: 48px;
		border: none;
		background-color: transparent;
		transform: translate(-50%, -50%);
	}
}

.video__button {
	outline: none;
}
.video__shepe {
	fill: #212121;
	fill-opacity: 0.8;
}
.video-main__item:hover .video__shepe,
.video__button:focus .video__shepe {
	fill: #ff0000;
	fill-opacity: 1;
}

.video--enabled {
	cursor: pointer;
}

.video--enabled .video__button {
	display: block;
}
