.portfolio {
	margin: 30px 0;
}

.portfolio__list {
	display: flex;
	flex-direction: column;
	row-gap: 20px;
}

.portfolio__img {
	position: relative;
	width: 100%;
	padding-bottom: 60%;
	& img {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	&::before {
		content: "";
		position: absolute;
		z-index: 2;
		width: 100%;
		height: 20px;
		bottom: 0;
		left: 0;
		background: rgba(34, 149, 70, 0.5);
	}
}

.portfolio__img-descr {
	color: #000;
	font-size: 16px;
	font-weight: 500;
	line-height: 145%; /* 23.2px */

	-webkit-line-clamp: 2; /* Число отображаемых строк */
	display: -webkit-box; /* Включаем флексбоксы */
	-webkit-box-orient: vertical; /* Вертикальная ориентация */
	overflow: hidden; /* Обрезаем всё за пределами блока */
}

@media (min-width: 961px) {
	.portfolio__list {
		display: flex;
		flex-wrap: wrap;
		flex-direction: row;
		gap: 20px;
	}

	.portfolio__item{
		flex: 1 0 45%;
	}
}
