///////////////////////////////////////////
/// 
.menu__icon {
	display: none;
}

.menu__list > li {
	position: relative;
	margin: 0px 0px 0px 20px;
	display: flex;
	align-items: center;
}
// .menu__item {
// 	padding-right: 15px;
// }
.menu__body button {
	background-color: transparent;
	text-align: left;
}
.menu__body a {
	color: $accent;
	font-size: 18px;
	font-style: normal;
	font-weight: 500;
	line-height: normal;
	text-transform: uppercase;
}

.sub-menu {
	position: absolute;
	z-index: 1;
	top: 100%;
	left: -10px;
	background-color: #5f5f5f07;
	padding: 15px;
	min-width: 200px;
}
.sub-menu li {
	margin: 0px 0px 10px 0px;
}
.sub-menu li:last-child {
	margin: 0;
}
.sub-menu a {
	color: $accent;
	font-size: 20px;
	letter-spacing: 1.5px;
	font-style: normal;
	font-weight: 500;
	line-height: normal;
	text-transform: uppercase;
}

.menu__arrow {
	display: inline-block;
	height: 10px;
	width: 10px;
	background-image: url(../img/icons/down-arrow.svg);
	background-repeat: no-repeat;
	background-position: center;
	object-fit: contain;
	margin-left: 6px;
}
// /* ----------------------- */
.menu__list li:hover .menu__arrow {
	transform: rotate(-180deg);
}

body._pc .menu__list > li:hover .sub-menu {
	opacity: 1;
	visibility: visible;
	transform: translate(0px, 0px);
	pointer-events: all;
}
body._touch .menu__list > li {
	display: flex;
	align-items: center;
}
body._touch .menu__list a {
	flex: 1 1 auto;
}
body._touch .menu__list > li .menu__arrow {
	transform: rotate(0deg);
}

body._touch .menu__list > li._active .sub-menu {
	opacity: 1;
	visibility: visible;
	transform: translate(0px, 0px);
	pointer-events: all;
}
body._touch .menu__list > li._active .menu__arrow {
	transform: rotate(-180deg);
}

// /* ----------------------- */
@media (min-width: 767px) {
	.menu__list {
		display: flex;
		align-items: center;
	}
	.menu__list > li {
		padding: 10px 0;
	}
	.sub-menu {
		transform: translate(0px, 10%);
		opacity: 0;
		visibility: hidden;
		pointer-events: none;
		background-color: #f0f0f0;
		transition: all 0.3s ease 0s;
	}
}
@media (max-width: 767px) {
	._pc .menu__list > li:hover .sub-menu {
		display: block;
	}
	.menu__icon {
		z-index: 5;
		display: block;
		position: relative;
		width: 30px;
		height: 18px;
		cursor: pointer;
	}
	.menu__icon span,
	.menu__icon::before,
	.menu__icon::after {
		left: 0;
		position: absolute;
		height: 10%;
		width: 100%;
		transition: all 0.3s ease 0s;
		background-color: $accent;
	}
	.menu__icon::before,
	.menu__icon::after {
		content: "";
	}
	.menu__icon::before {
		top: 0;
	}
	.menu__icon::after {
		bottom: 0;
	}
	.menu__icon span {
		top: 50%;
		transform: scale(1) translate(0px, -50%);
	}

	.menu__icon._active span {
		transform: scale(0) translate(0px, -50%);
	}
	.menu__icon._active::before {
		top: 50%;
		transform: rotate(-45deg) translate(0px, -50%);
	}
	.menu__icon._active::after {
		bottom: 50%;
		transform: rotate(45deg) translate(0px, 50%);
	}

	.menu__body {
		position: fixed;
		z-index: 4;
		top: 0;
		left: -100%;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.9);
		padding: 100px 30px 30px 30px;
		overflow: auto;
	}
	.menu__body._active {
		left: 0;
		padding-top: 271px;
	}

	.menu__list > li {
		flex-wrap: wrap;
		margin: 0px 0px 30px 0px;
	}
	.menu__list > li:last-child {
		margin-bottom: 0;
	}
	.menu__list > li._active .sub-menu {
		display: block;
	}
	.menu__body a {
		font-size: 24px;
	}
	.sub-menu {
		position: relative;
		background-color: rgba(0, 0, 0, 0.3);
		flex: 1 1 100%;
		margin: 20px 0px 0px 0px;
		display: none;
	}
}

@media (max-width: 641px) {
	.menu__body._active {
		padding-top: 42%;
	}
}
