@charset "UTF-8";

@font-face {
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-Extra.woff") format("woff"), url("../fonts/Inter-Extra.woff2") format("woff2");
  font-weight: 800;
  font-style: normal;
}

@font-face {
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-Bold.woff") format("woff"), url("../fonts/Inter-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-SemiBold.woff") format("woff"), url("../fonts/Inter-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-Medium.woff") format("woff"), url("../fonts/Inter-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-Regular.woff") format("woff"), url("../fonts/Inter-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-Light.woff") format("woff"), url("../fonts/Inter-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
}

* {
  padding: 0px;
  margin: 0px;
  border: 0px;
  transition: all 0s ease 0s;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  line-height: 1;
  font-family: "inter";
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: "inter";
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

body {
  color: #000;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
}

body._lock {
  overflow: hidden;
}

.wrapper {
  position: relative;
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

._container {
  max-width: 1150px;
  padding: 0 15px;
  margin: 0 auto;
}

._container_item {
  max-width: 740px;
  padding: 0 15px;
  margin: 0 auto;
}

.page__title {
  position: relative;
  max-width: 740px;
  padding-left: 15px;
  color: #000;
  font-size: 18px;
  line-height: 20px;
  font-weight: 700;
  text-transform: uppercase;
}

.page__title::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 5px;
  border-radius: 67px;
  background: #be0026;
}

.sponsor {
  width: 100%;
}

.sponsor__link {
  display: block;
  position: relative;
  width: 100%;
  padding-bottom: 40%;
  max-height: 250px;
  background-image: linear-gradient(300deg, #ccb5b5, #002751);
}

.sponsor__link img {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  height: 80%;
  object-fit: contain;
  transition: all 0.3s ease-in-out;
}

.sponsor__link:hover .sponsor__title {
  text-shadow: 3px 3px 0 #1c1c1c;
}

.sponsor__title {
  position: absolute;
  bottom: 10%;
  right: 10%;
  font-size: 30px;
  color: #fff;
  text-shadow: 3px 3px 0 #7a7a7a;
  transition: text-shadow ease-in 0.3s;
}

.sponsor__link:hover img {
  width: 82%;
  height: 82%;
}

.header__head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: solid 4px #229546;
  border-bottom: solid 4px #229546;
}

.header__logo a {
  position: relative;
  display: block;
  width: 160px;
  height: 59px;
}

.header__logo a img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.header__decription {
  display: none;
  padding-right: 10px;
  color: #229546;
  text-align: right;
  text-shadow: 1px 1px 1px rgba(255, 255, 255, 0.78);
  font-size: 15px;
  line-height: 1.1;
  font-style: normal;
  font-weight: 500;
  text-transform: uppercase;
  border-right: 13px solid #be0026;
}

.menu__icon {
  display: none;
}

.menu__list > li {
  position: relative;
  margin: 0px 0px 0px 20px;
  display: flex;
  align-items: center;
}

.menu__body button {
  background-color: transparent;
  text-align: left;
}

.menu__body a {
  color: #229546;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
}

.sub-menu {
  position: absolute;
  z-index: 1;
  top: 100%;
  left: -10px;
  background-color: rgba(95, 95, 95, 0.0274509804);
  padding: 15px;
  min-width: 200px;
}

.sub-menu li {
  margin: 0px 0px 10px 0px;
}

.sub-menu li:last-child {
  margin: 0;
}

.sub-menu a {
  color: #229546;
  font-size: 20px;
  letter-spacing: 1.5px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
}

.menu__arrow {
  display: inline-block;
  height: 10px;
  width: 10px;
  background-image: url(../img/icons/down-arrow.svg);
  background-repeat: no-repeat;
  background-position: center;
  object-fit: contain;
  margin-left: 6px;
}

.menu__list li:hover .menu__arrow {
  transform: rotate(-180deg);
}

body._pc .menu__list > li:hover .sub-menu {
  opacity: 1;
  visibility: visible;
  transform: translate(0px, 0px);
  pointer-events: all;
}

body._touch .menu__list > li {
  display: flex;
  align-items: center;
}

body._touch .menu__list a {
  flex: 1 1 auto;
}

body._touch .menu__list > li .menu__arrow {
  transform: rotate(0deg);
}

body._touch .menu__list > li._active .sub-menu {
  opacity: 1;
  visibility: visible;
  transform: translate(0px, 0px);
  pointer-events: all;
}

body._touch .menu__list > li._active .menu__arrow {
  transform: rotate(-180deg);
}

.slider-section {
  margin: 20px 0;
  padding-top: 30px;
  background-image: url(../img/bg-main-slider.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.slider-section__title {
  color: #fff;
  font-size: 24px;
  font-weight: 700;
  line-height: normal;
}

.slider-section__wrap {
  display: flex;
  flex-direction: column;
  row-gap: 30px;
  margin: 0 -15px;
}

.slider-section__sliders {
  width: 100%;
  position: relative;
  display: flex;
}

.slider {
  margin-top: 20px;
  width: 100%;
  overflow: hidden;
}

.slider__wrapper {
  display: flex;
  width: 100%;
}

.slid {
  flex-shrink: 0;
  cursor: pointer;
  display: flex;
  flex-direction: column;
}

.slid__title {
  margin-bottom: 13px;
  padding: 0 15px;
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  color: #fff;
  -webkit-line-clamp: 2;
  /* Число отображаемых строк */
  display: -webkit-box;
  /* Включаем флексбоксы */
  -webkit-box-orient: vertical;
  /* Вертикальная ориентация */
  overflow: hidden;
  /* Обрезаем всё за пределами блока */
}

.slid__img {
  position: relative;
  flex: 1 0 400px;
  height: 400px;
  border-radius: 0px 5px 5px 0px;
  overflow: hidden;
}

.slid__img img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slid:hover .slid__img img {
  transition: scale 0.8s ease-in;
  scale: 1.05;
}

.slid:active {
  cursor: grabbing;
}

.slider-preview {
  display: none;
}

.widget {
  display: none;
}

.weather__city {
  font-weight: 400;
  margin-top: 5px;
  color: #BE0026;
}

.posting {
  padding: 40px 0;
}

.post__title {
  padding-left: 15px;
  border-left: 5px solid #BE0026;
  color: #000;
  font-size: 20px;
  font-weight: 600;
}

.post__text {
  max-width: 730px;
  margin: 15px 0;
  color: #000;
  font-size: 18px;
  font-weight: 300;
}

.post__img {
  position: relative;
  width: 100%;
  padding-bottom: 62%;
}

.post__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.leaders {
  background: #d9d9d9;
  padding-top: 50px;
  padding-bottom: 100px;
}

.leaders__title {
  padding-bottom: 50px;
  font-size: clamp(23px, 5vw, 25px);
  color: #000;
  text-align: center;
  font-weight: 600;
  text-transform: uppercase;
}

.leaders__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  text-align: center;
  gap: 70px;
}

.leader {
  flex: 0 1 500px;
}

.leader__img {
  position: relative;
  width: 228px;
  height: 228px;
  border-radius: 50%;
  overflow: hidden;
  margin: 0 auto;
  margin-bottom: 12px;
}

.leader__img img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.leader__name {
  position: relative;
  border-bottom: solid 5px #229546;
  min-height: 50px;
  padding-bottom: 4px;
  color: #000;
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  text-transform: uppercase;
}

.leader__name span {
  position: absolute;
  width: 100%;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.leader__post {
  min-height: 40px;
  position: relative;
  padding-top: 12px;
  color: #000;
  text-align: center;
  font-size: 20px;
  font-weight: 400;
}

.leader__post span {
  padding-top: 15px;
  position: absolute;
  width: 100%;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.footer {
  background-color: #229546;
  padding: 62px 0 15px;
}

.contacts__title {
  max-width: 500px;
  padding-bottom: 15px;
  margin-bottom: 8px;
  border-bottom: 5px solid #fff;
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  text-transform: uppercase;
}

.data-item {
  position: relative;
  padding-left: 30px;
  color: #fff;
  font-size: 18px;
  font-weight: 400;
}

.data-item:not(:last-child) {
  margin-bottom: 11px;
}

.data-item::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 22px;
  width: 30px;
  background-position: center;
  background-repeat: no-repeat;
  object-fit: contain;
}

.data-item_location::before {
  background-image: url(../img/icons/geo.svg);
}

.data-item_tel::before {
  background-image: url(../img/icons/tel.svg);
}

.data-item_mail::before {
  background-image: url(../img/icons/mail.svg);
}

.data-item_tel a,
.data-item_mail a {
  color: #fff;
  font-size: 18px;
  font-weight: 400;
}

.footer__logo {
  margin: 30px 0;
}

.social-item {
  display: inline;
}

.social-item:not(:last-child) {
  margin-right: 20px;
}

.footer__copyright {
  margin-top: 30px;
  color: #fff;
  text-align: center;
  font-size: 25px;
  font-style: normal;
  font-weight: 600;
}

.widget {
  margin-top: 30px;
  color: #fff;
  font-weight: 200;
  font-size: 14px;
  padding-bottom: 0px;
}

.widget-link {
  margin-top: 35px;
  display: block;
  color: #fff;
  font-weight: 200;
  font-size: 14px;
}

.list-news {
  padding: 30px 0;
  position: relative;
}

.list-news_title {
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: 700;
}

.news-list {
  width: 100%;
}

.news-list__item {
  width: 100%;
  padding: 10px 0;
}

.news-list__img {
  position: relative;
  width: 100%;
  padding-bottom: 100%;
  object-fit: cover;
}

.news-list__img img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.news-list__date {
  position: relative;
  margin: 5px 0 10px 0;
  color: #9b9b9b;
  font-size: 13px;
  font-weight: 500;
}

.news-list__date::after {
  content: "";
  margin-left: 65px;
  position: absolute;
  width: calc(100% - 65px);
  height: 3px;
  background-color: #229546;
  left: 0;
  top: 5px;
}

.news-list__title {
  color: #000;
  font-size: 16px;
  font-weight: 500;
  -webkit-line-clamp: 3;
  /* Число отображаемых строк */
  display: -webkit-box;
  /* Включаем флексбоксы */
  -webkit-box-orient: vertical;
  /* Вертикальная ориентация */
  overflow: hidden;
  /* Обрезаем всё за пределами блока */
}

.news-list__descr {
  margin-top: 10px;
  color: #000;
  font-size: 16px;
  font-weight: 300;
  -webkit-line-clamp: 10;
  /* Число отображаемых строк */
  display: -webkit-box;
  /* Включаем флексбоксы */
  -webkit-box-orient: vertical;
  /* Вертикальная ориентация */
  overflow: hidden;
  /* Обрезаем всё за пределами блока */
}

.news-item {
  padding: 20px 0;
}

.news-item h1 {
  margin: 10px 0;
  font-size: 25px;
  font-weight: 500;
}

.news-item p {
  margin: 10px 0;
  text-align: justify;
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
}

.news-item figure {
  margin: 10px 0;
}

.news-item img {
  width: 100%;
  height: auto;
}

.news-item figcaption {
  margin-top: 7px;
  text-align: center;
  color: #484848;
  font-size: 14px;
}

.portfolio {
  margin: 30px 0;
}

.portfolio__list {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}

.portfolio__img {
  position: relative;
  width: 100%;
  padding-bottom: 60%;
}

.portfolio__img img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.portfolio__img::before {
  content: "";
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 20px;
  bottom: 0;
  left: 0;
  background: rgba(34, 149, 70, 0.5);
}

.portfolio__img-descr {
  color: #000;
  font-size: 16px;
  font-weight: 500;
  line-height: 145%;
  /* 23.2px */
  -webkit-line-clamp: 2;
  /* Число отображаемых строк */
  display: -webkit-box;
  /* Включаем флексбоксы */
  -webkit-box-orient: vertical;
  /* Вертикальная ориентация */
  overflow: hidden;
  /* Обрезаем всё за пределами блока */
}

.portfolio-item__title {
  padding: clamp(20px, 5vw, 45px) 0;
  color: #000;
  font-size: clamp(20px, 5vw, 45px);
  font-weight: 500;
}

.portfolio-item__grid {
  padding: 30px 0;
  display: grid;
  grid-template-columns: fr;
  grid-gap: 20px;
}

.portfolio-item__grid-item {
  height: 300px;
  background-color: #333;
}

.portfolio-item__grid-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.portfolio-item__grid-item iframe,
.portfolio-item__grid-item video {
  width: 100%;
  height: 100%;
  border: none;
}

:root {
  /* colors */
  --main-bg: #151515;
  --main-text: #fff;
  --second-text: #8d97a4;
  --accent: #bd082a;
  --accent-active: #8f0d24;
  /* container */
  --padding: 15px;
  --max-width-container: 1440px;
}

.contacts {
  padding-top: 30px;
}

.contacts__page-title {
  margin-bottom: 20px;
}

.contacts__descr {
  max-width: 740px;
  margin-bottom: 30px;
}

.contacts__content-block {
  display: flex;
  flex-direction: column;
  row-gap: 40px;
}

.contact-data__item:not(:last-child) {
  margin-bottom: 15px;
}

.contacts__map {
  margin-top: 50px;
}

.button {
  border: none;
  border-radius: 4px;
  padding: 13px 25px;
  font-family: inherit;
  font-size: 20px;
  color: var(--main-text);
  background-color: var(--accent);
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.button:hover {
  background-color: var(--accent-active);
}

.link {
  font-size: 20px;
  color: var(--main-text);
  transition: color 0.3s ease-in-out;
}

.link:hover {
  color: var(--accent);
}

.card {
  border: 1px solid var(--second-text);
  border-radius: 4px;
  padding: 15px;
  color: var(--main-text);
}

.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: var(--padding);
}

.flex {
  display: flex;
  flex-wrap: wrap;
}

.divider {
  margin: 20px 0;
  width: 100%;
  height: 2px;
  background-color: var(--second-text);
}

.input {
  border: 1px solid var(--second-text);
  border-radius: 4px;
  padding: 0 20px;
  width: 300px;
  height: 50px;
  font-family: inherit;
  color: #fff;
  background-color: transparent;
}

.input::placeholder {
  color: var(--second-text);
}

.textarea {
  border: 1px solid var(--second-text);
  border-radius: 4px;
  padding: 20px;
  width: 300px;
  font-family: inherit;
  color: #fff;
  background-color: transparent;
}

.textarea::placeholder {
  color: var(--second-text);
}

.form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.input {
  margin-bottom: 20px;
}

.textarea {
  margin-bottom: 20px;
}

.form__label {
  display: flex;
  flex-direction: column-reverse;
}

.about-page {
  padding: 30px 0;
}

.about-page__title {
  position: relative;
  max-width: 740px;
  padding-left: 15px;
  color: #000;
  font-size: 18px;
  line-height: 20px;
  font-weight: 700;
  text-transform: uppercase;
}

.about-page__title::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 5px;
  border-radius: 67px;
  background: #be0026;
}

.about-page__title-descr {
  padding: 15px 0 25px;
  max-width: 740px;
  color: #000;
  font-size: 14px;
  font-weight: 300;
}

.about-page__item {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}

.about-page__item:not(:last-child) {
  margin-bottom: 25px;
}

.about-page__text-block {
  color: #000;
  text-align: justify;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
}

.about-page__text-block p:not(:last-child) {
  margin-bottom: 15px;
}

.about-page__text-block p span {
  color: #f00;
}

.about-page__img {
  position: relative;
  width: 100%;
  overflow: hidden;
  flex: 0 0 300px;
}

.about-page__img img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.about-page__list {
  font-weight: 500;
}

.about-page__list li {
  padding-left: 45px;
  font-weight: 400;
}

.about-page__list li:first-child {
  margin-top: 10px;
}

.about-page__list li:not(:last-child) {
  margin-bottom: 10px;
}

.cooperation-page {
  padding: 30px 0;
}

.cooperation-page__title {
  max-width: 890px;
}

.cooperation-page__list {
  display: flex;
  flex-direction: column;
  row-gap: 30px;
  padding: 30px 0;
}

.cooperation-page__item {
  display: flex;
  flex-direction: column-reverse;
  row-gap: 20px;
}

.cooperation-page__text-block h2 {
  margin-bottom: 25px;
  color: #000;
  font-size: 18px;
  font-weight: 500;
