.sponsor {
	width: 100%;
	&__link {
		display: block;
		position: relative;
		width: 100%;
		padding-bottom: 40%;
		max-height: 250px;
		background-image: linear-gradient(300deg, #ccb5b5, #002751);
		
		& img {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			width: 80%;
			height: 80%;
			object-fit: contain;
			transition: all 0.3s ease-in-out;
		}
		&:hover .sponsor__title {
			text-shadow: 3px 3px 0 #1c1c1c;
		}
	}
	&__title {
		position: absolute;
		bottom: 10%;
		right: 10%;
		font-size: 30px;
		color: #fff;
		text-shadow: 3px 3px 0 #7a7a7a;
		transition: text-shadow ease-in 0.3s;
	}
}

.sponsor__link:hover img {
	width: 82%;
	height: 82%;
}

@media (min-width: 641px) {
	.sponsor__link {
		padding-bottom: 0%;
		height: 250px;
	}
}
