.footer {
	background-color: $accent;
	padding: 62px 0 15px;
}

.contacts__title {
	max-width: 500px;
	padding-bottom: 15px;
	margin-bottom: 8px;
	border-bottom: 5px solid #fff;
	color: #fff;
	font-size: 20px;
	font-weight: 600;
	text-transform: uppercase;
}

.data-item {
	position: relative;
	padding-left: 30px;
	color: #fff;
	font-size: 18px;
	font-weight: 400;
	&:not(:last-child) {
		margin-bottom: 11px;
	}
	&::before {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		height: 22px;
		width: 30px;
		background-position: center;
		background-repeat: no-repeat;
		object-fit: contain;
	}
}
.data-item_location::before {
	background-image: url(../img/icons/geo.svg);
}
.data-item_tel::before {
	background-image: url(../img/icons/tel.svg);
}
.data-item_mail::before {
	background-image: url(../img/icons/mail.svg);
}
.data-item_tel a,
.data-item_mail a {
	color: #fff;
	font-size: 18px;
	font-weight: 400;
}

.footer__logo {
	margin: 30px 0;
}

.social-item {
	display: inline;
	&:not(:last-child) {
		margin-right: 20px;
	}
}

.footer__copyright {
	margin-top: 30px;
	color: #fff;
	text-align: center;
	font-size: 25px;
	font-style: normal;
	font-weight: 600;
}

@media (min-width: 1025px) {
	.footer__wrap {
		display: flex;
		justify-content: space-between;
	}
}

.widget {
	margin-top: 30px;
	color: #fff;
	font-weight: 200;
	font-size: 14px;
	padding-bottom: 0px;
}

.widget-link {
	margin-top: 35px;
	display: block;
	color: #fff;
	font-weight: 200;
	font-size: 14px;
}
