.students-page {
	padding: 30px 0;
}

.students-page__data-item {
	margin-top: 30px;
	display: flex;
	flex-direction: column-reverse;
	row-gap: 10px;
	& p {
		color: #000;
		text-align: justify;
		font-size: 16px;
		font-weight: 300;
		line-height: 24px;
	}
}

.page__title_margin-top {
	margin-top: 30px;
}

.students-page__img {
	position: relative;
	width: 100%;
	padding-bottom: 82%;
	overflow: hidden;
	& img {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

@media (min-width: 961px) {
	.students-page__data-item {
		flex-direction: row;
		column-gap: 30px;
	}
	.students-page__img {
		padding-bottom: 0;
		flex: 0 0 50%;
	}
	.students-page__data-paragraf {
		position: relative;
		&::after {
			content: "";
			position: absolute;
			right: calc(-30px / 2 - 3px / 2);
			top: 0;
			height: 100%;
			width: 3px;
			border-radius: 3px;
			background-color: $accent;
		}
	}
}

@media (min-width: 1025px) {
	.students-page__data-item {
		column-gap: 80px;
	}

	.students-page__img {
		min-height: 470px;
	}
	.students-page__data-paragraf {
		&::after {
			right: calc(-80px / 2 - 3px / 2);;
		}
	}
}


.students-page__data-descr {
	margin: 30px 0;
	display: flex;
	flex-direction: column;

	color: #000;
	text-align: justify;
	font-size: 16px;
	font-weight: 300;
	line-height: 24px;
}

.students-page__img-descr {
	position: relative;
	width: 100%;
	overflow: hidden;
	flex: 0 0 300px;
	& img {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		object-fit: contain;
	}
}

.students-page__data-paragraf {
	& p:not(:last-child) {
		margin-bottom: 30px;
	}
}

@media (min-width: 641px) {
	.students-page__data-descr {
		flex-direction: row;
		column-gap: 30px;
	}
}
