.portfolio-item__title {
	padding: clamp(20px, 5vw, 45px) 0;
	color: #000;
	font-size: clamp(20px, 5vw, 45px);
	font-weight: 500;
}

.portfolio-item__grid {
	padding: 30px 0;
	display: grid;
	grid-template-columns: fr;
	grid-gap: 20px;
}

.portfolio-item__grid-item {
	height: 300px;
	background-color: #333;
}

.portfolio-item__grid-item img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.portfolio-item__grid-item iframe,
.portfolio-item__grid-item video {
	width: 100%;
	height: 100%;
	border: none;
}

@media (min-width: 641px) {
	.portfolio-item__grid {
		grid-template-columns: repeat(2, 1fr);
	}
}

@media (min-width: 961px) {
	.portfolio-item__grid {
		grid-template-columns: repeat(3, 1fr);
	}
}
