:root {
	/* colors */
	--main-bg: #151515;
	--main-text: #fff;
	--second-text: #8d97a4;
	--accent: #bd082a;
	--accent-active: #8f0d24;

	/* container */
	--padding: 15px;
	--max-width-container: 1440px;
}

.contacts {
	padding-top: 30px;
}

.contacts__page-title {
	margin-bottom: 20px;
}

.contacts__descr {
	max-width: 740px;
	margin-bottom: 30px;
}

.contacts__content-block {
	display: flex;
	flex-direction: column;
	row-gap: 40px;
}

.contact-data__item:not(:last-child) {
	margin-bottom: 15px;
}

.contacts__map{
	margin-top: 50px;
}

@media (min-width: 641px) {
	.contacts__content-block {
		display: flex;
		flex-direction: row;
		column-gap: 40px;
	}
}

.button {
	border: none;
	border-radius: 4px;
	padding: 13px 25px;
	font-family: inherit;
	font-size: 20px;
	color: var(--main-text);
	background-color: var(--accent);
	cursor: pointer;
	transition: background-color 0.3s ease-in-out;
}

.button:hover {
	background-color: var(--accent-active);
}

.link {
	font-size: 20px;
	color: var(--main-text);
	transition: color 0.3s ease-in-out;
}

.link:hover {
	color: var(--accent);
}

.card {
	border: 1px solid var(--second-text);
	border-radius: 4px;
	padding: 15px;
	color: var(--main-text);
}

.grid {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: var(--padding);
}

.flex {
	display: flex;
	flex-wrap: wrap;
}

.divider {
	margin: 20px 0;
	width: 100%;
	height: 2px;
	background-color: var(--second-text);
}

.input {
	border: 1px solid var(--second-text);
	border-radius: 4px;
	padding: 0 20px;
	width: 300px;
	height: 50px;
	font-family: inherit;
	color: #fff;
	background-color: transparent;
}

.input::placeholder {
	color: var(--second-text);
}

.textarea {
	border: 1px solid var(--second-text);
	border-radius: 4px;
	padding: 20px;
	width: 300px;
	font-family: inherit;
	color: #fff;
	background-color: transparent;
}

.textarea::placeholder {
	color: var(--second-text);
}

.form {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.input {
	margin-bottom: 20px;
}

.textarea {
	margin-bottom: 20px;
}

.form__label {
	display: flex;
	flex-direction: column-reverse;
}
