.slider-section {
	margin: 20px 0;
	padding-top: 30px;
	background-image: url(../img/bg-main-slider.jpg);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
}

.slider-section__title {
	color: #fff;
	font-size: 24px;
	font-weight: 700;
	line-height: normal;
}

.slider-section__wrap {
	display: flex;
	flex-direction: column;
	row-gap: 30px;
	margin: 0 -15px;
}

.slider-section__sliders {
	width: 100%;
	position: relative;
	display: flex;
}

.slider {
	margin-top: 20px;
	width: 100%;
	overflow: hidden;
}
.slider__wrapper {
	display: flex;

	width: 100%;
}

.slid {
	flex-shrink: 0;
	cursor: pointer;
	display: flex;
	flex-direction: column;
	&__title {
		margin-bottom: 13px;
		padding: 0 15px;
		color: #fff;
		font-size: 20px;
		font-weight: 300;
		color: #fff;
		-webkit-line-clamp: 2; /* Число отображаемых строк */
		display: -webkit-box; /* Включаем флексбоксы */
		-webkit-box-orient: vertical; /* Вертикальная ориентация */
		overflow: hidden; /* Обрезаем всё за пределами блока */
	}
	&__img {
		position: relative;
		flex: 1 0 400px;
		height: 400px;
		border-radius: 0px 5px 5px 0px;
		overflow: hidden;
		& img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	&:hover .slid__img img {
		transition: scale 0.8s ease-in;
		scale: 1.05;
	}
}

.slid:active {
	cursor: grabbing;
}
.slider-preview {
	display: none;
}

@media (min-width: 641px) {
	.widget {
		display: block;
		padding-bottom: 20px;
		&__title {
			padding: 0 15px;
			margin-bottom: 20px;
			color: #fff;
			font-size: 24px;
			font-weight: 500;
			line-height: normal;
		}
		&__list {
			display: flex;
			gap: 20px;
			flex-wrap: wrap;
			padding: 0 15px;
			& span {
				display: block;
			}
		}
		&__item {
			position: relative;
			height: 60px;
			flex: 0 1 300px;
			border-radius: 5px;
			background: rgba(255, 255, 255, 0.95);
			box-shadow: 4px 4px 5px 0px rgba(0, 0, 0, 0.25);
		}
		&__widget {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			text-align: center;
			&_temp {
				width: 100%;
				color: $accent-red;
				font-size: 20px;
				font-weight: 600;
				& span {
					margin-top: 8px;
					font-size: 16px;
					font-weight: 400;
				}
			}
			&_exchange {
				width: 100%;
				color: $accent-red;
				color: #be0026;
				font-size: 16px;
				font-weight: 500;
				& span {
					&:not(:last-child) {
						margin-bottom: 10px;
					}
				}
			}
			&_time {
				color: $accent-red;
				font-size: 16px;
				font-weight: 600;
				width: 100%;
			}
		}
	}
}

@media (min-width: 961px) {
	.slider-section__sliders {
		padding-bottom: 100px;
	}

	.slider {
		width: 700px;
	}
	.slid {
		&__img {
			flex: 1 0 390px;
		}
	}
	.slider-preview {
		position: absolute;
		z-index: 2;
		bottom: -18px;
		left: 35px;
		display: block;
		width: 715px;
		overflow: hidden;
		&__wrapper {
			display: flex;
			width: 100%;
		}
	}
	.slid-preview {
		flex-shrink: 0;
		cursor: pointer;
		&__img {
			position: relative;
			width: 225px;
			height: 160px;
			overflow: hidden;
			border-radius: 0px 0px 5px 5px;
			& img {
				position: absolute;
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
				object-fit: cover;
				background-position: center;
				border-bottom: 5px solid #be0026;
			}
		}
		&__title {
			position: absolute;
			padding: 14px 20px;
			bottom: 0;
			border-radius: 0px 0px 5px 5px;
			background: rgba(0, 0, 0, 0.7);
			border-bottom: $accent-red 10px solid;
			p {
				color: #fff;
				-webkit-line-clamp: 2; /* Число отображаемых строк */
				display: -webkit-box; /* Включаем флексбоксы */
				-webkit-box-orient: vertical; /* Вертикальная ориентация */
				overflow: hidden; /* Обрезаем всё за пределами блока */
				font-size: 13px;
				font-weight: 500;
			}
		}
	}
}
.widget {
	display: none;
}

@media (min-width: 961px) {
	.slider-section__wrap {
		column-gap: 50px;
		flex-direction: row;
	}
	.slider-section__sliders {
		width: auto;
	}

	.widget {
		position: relative;
		width: 100%;
		&__title {
			position: absolute;
			width: 100%;
			top: -25px;
			// padding: 0 15px;!!!!!!!!!!!!!!!!!!!
		}
		&__list {
			padding-top: 54px;
		}
		&__item {
			height: 70px;
			flex: 0 0 130px;
		}
	}
}

.weather__city{
	font-weight: 400;
	margin-top: 5px;
	color: $accent-red;
}
